.about {
  padding-top: 90px;
  padding-bottom: 80px;

  @include md-down {
    padding-top: 60px;
    padding-bottom: 40px;
  }

  @include xs-down {
    padding-top: 50px;
    padding-bottom: 30px;
  }

  &__title {
    margin-bottom: 60px;

    @include md-down {
      margin-bottom: 40px;
    }

    @include xs-down {
      margin-bottom: 30px;
    }
  }

  &__content {
    background-image: linear-gradient(to right, rgba(#414b50, 0.2) 60%, #ffffff 0%);
    background-position: bottom;
    background-size: 8px 1px;
    background-repeat: repeat-x;
    padding-bottom: 40px;
    margin-bottom: 40px;

    @include md-down {
      padding-bottom: 30px;
      margin-bottom: 30px;
    }

    @include xs-down {
      padding-bottom: 20px;
      margin-bottom: 20px;
    }

    p {
      position: relative;
      font-size: 24px;
      font-weight: 300;
      line-height: 1.4;
      padding-left: 55px;
      margin-top: 0;
      margin-bottom: 40px;

      @include md-down {
        font-size: 20px;
        margin-bottom: 30px;
      }

      @include xs-down {
        font-size: 16px;
        margin-bottom: 20px;
        padding-left: 25px;
      }

      &:last-child {
        margin-bottom: 0;
      }

      &:before {
        content: '';
        position: absolute;
        top: 13px;
        left: 10px;
        width: 25px;
        height: 2px;
        background-color: #0a9bdc;

        @include xs-down {
          top: 10px;
          left: 0;
          width: 15px;
        }
      }

      span {
        font-weight: 400;
        background-color: #e1f5ff;
      }
    }
  }

  &__solutions {

    h3 {
      font-size: 16px;
      font-weight: 400;
      font-style: italic;
      letter-spacing: 0.5px;
      text-transform: uppercase;
      color: rgba(#414b50, 0.5);
      margin-top: 0;
      margin-bottom: 40px;

      @include md-down {
        margin-bottom: 30px;
      }
    }

    ul {
      display: flex;
      flex-wrap: wrap;
      list-style-type: none;
      padding-left: 0;
      margin-top: 0;
      margin-bottom: 0;

      @include sm-down {
        display: block;
      }
    }

    li {
      flex: 0 0 50%;
      display: flex;
      margin-bottom: 40px;

      @include xs-down {
        margin-bottom: 20px;
      }

      &:last-child {

        @include xs-down {
          margin-bottom: 0;
        }
      }
    }

    p {
      max-width: 330px;
      font-size: 16px;
      font-weight: 300;
      line-height: 1.4;
      margin-top: 0;
      margin-bottom: 0;

      @include md-down {
        max-width: 280px;
      }

      @include sm-down {
        max-width: 100%;
      }

      @include xs-down {
        font-size: 14px;
      }

      span {
        font-weight: 400;
        background-color: #e1f5ff;
      }
    }
  }

  &__icon-wrapper {
    flex: 0 0 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    color: #ffffff;
    background-color: #0c89de;
    border-radius: 50%;
    margin-right: 15px;

    svg {
      font-size: 20px;

      @include sm-down {
        font-size: 16px;
      }
    }

    &_like {

      svg {
        font-size: 18px;

        @include sm-down {
          font-size: 14px;
        }
      }
    }
  }
}