.team {
  position: relative;
  min-height: 800px;
  padding-top: 90px;

  @include md-down {
    min-height: initial;
    background-color: rgba(14, 175, 227, 0.97);
    padding-top: 40px;
    padding-bottom: 40px;
  }

  &__inner {
    position: relative;
    z-index: 2;
  }

  &__title {
    margin-bottom: 40px;
  }

  &__member {
    max-width: 760px;
    display: flex;
    line-height: 1.4;
    background-image: linear-gradient(to right, rgba(#fff, 0.4) 60%, rgba(#fff, 0) 0%);
    background-position: bottom;
    background-size: 8px 1px;
    background-repeat: repeat-x;
    padding-bottom: 20px;
    margin-bottom: 20px;

    @include lg-down {
      max-width: 700px;
    }

    @include xs-down {
      display: block;
    }

    &:last-child {
      background: none;
      padding-bottom: 0;
      margin-bottom: 0;
    }
  }

  &__avatar {
    flex: 0 0 100px;
    position: relative;
    width: 100px;
    height: 100px;
    margin-right: 30px;

    @include xs-down {
      margin-right: auto;
      margin-bottom: 30px;
      margin-left: auto;
    }

    img {
      vertical-align: top;
      max-width: 100%;
      border-radius: 50%;
    }

    a {
      position: absolute;
      right: 0;
      bottom: 0;
      display: inline-block;
      vertical-align: top;
      text-decoration: none;
      color: #ffffff;
      background-color: #0a9bdc;
      border-radius: 3px;
      box-shadow: 0 0 10px 0 rgba(0, 127, 186, 0.5);
      padding-top: 8px;
      padding-right: 8px;
      padding-bottom: 8px;
      padding-left: 8px;
      transition: 0.3s;

      &:hover {
        background-color: lighten(#0a9bdc, 10%);
      }

      &:focus, &:active {
        background-color: darken(#0a9bdc, 10%);
      }

      svg {
        display: block;
      }
    }
  }

  &__info {

    h3 {
      font-size: 16px;
      letter-spacing: 0.5px;
      font-weight: 400;
      text-transform: uppercase;
      color: #ffffff;
      margin-top: 0;
      margin-bottom: 10px;

      span {
        display: inline-block;
        vertical-align: top;
        font-weight: 300;
        text-transform: capitalize;
        color: #ffffff;
        margin-left: 5px;
        opacity: 0.6;
      }
    }

    p {
      max-width: 665px;
      font-weight: 300;
      color: #ffffff;
      margin-top: 0;
      margin-bottom: 0;
      opacity: 0.8;

      @include xs-down {
        text-align: left;
      }
    }
  }

  &__background {
    position: absolute;
    width: 100%;
    height: 760px;

    @media only screen and (min-width: 1921px) {
      max-width: 100%;
    }

    @include md-down {
      display: none;
    }

    img {
      position: absolute;

      &:not(.centered-rellax) {
        top: 50%;
        left: 50%;
        min-width: 1920px;
        height: 100%;
        transform: translate(-50%, -50%);
      }

      @media only screen and (min-width: 1921px) {
        width: 100%;
      }

      &.centered-rellax {
        top: -150px;
        right: 0;
        left: auto;
        width: 1100px;
      }
    }

    &_left {
      top: 60px;
      left: 0;
      z-index: 1;
      overflow: hidden;

      svg {
        position: absolute;
        bottom: 6px;
        right: 50%;
        font-size: 160px;
        color: rgba(255, 255, 255, 0.3);
        transform: translateX(529px);
      }
    }

    &_right {
      top: 30px;
      right: 0;
      overflow: hidden;

      @include lg-down {
        width: 1120px;
      }
    }
  }

  &__background-image-wrapper {
    position: relative;
    width: 100%;
    height: 760px;
    overflow: hidden;
  }
}