.intro {
  position: relative;
  min-height: 610px;

  @include md-down {
    min-height: initial;
    background-color: rgba(10, 155, 220, 0.97);
    padding-top: 50px;
    padding-bottom: 50px;
  }

  &__inner {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;

    @include md-down {
      display: block;
    }
  }

  &__side {
    display: none;
    animation-delay: 1s;
    animation-duration: 1s;

    &.animated {
      display: block;
    }

    p {
      color: #ffffff;
      margin-top: 0;
      margin-bottom: 0;
    }

    &_left {
      padding-top: 148px;

      @include md-down {
        padding-top: 0;
        margin-bottom: 50px;
      }

      p {
        max-width: 385px;
        font-size: 36px;
        line-height: 1.4;

        @include md-down {
          max-width: 100%;
          font-size: 30px;
        }

        @include xs-down {
          font-size: 22px;
        }

        span {
          opacity: 0.8;
        }
      }
    }

    &_right {
      padding-top: 168px;

      @include md-down {
        display: flex;
        justify-content: flex-end;
        padding-top: 0;
      }

      &.animated {

        @include md-down {
          display: flex;
        }
      }

      h2 {
        position: relative;
        font-size: 20px;
        font-weight: 500;
        text-transform: uppercase;
        letter-spacing: 0.5px;
        color: #ffffff;
        margin-top: 0;
        margin-bottom: 20px;

        @include xs-down {
          font-size: 18px;
        }

        &:before {
          content: '';
          position: absolute;
          top: 50%;
          left: -35px;
          width: 25px;
          height: 2px;
          background-color: #ffffff;
          transform: translateY(-50%);
        }
      }

      p {
        position: relative;
        max-width: 400px;
        font-size: 24px;
        font-weight: 300;
        color: rgba(#fff, 0.8);
        line-height: 1.4;

        @include md-down {
          max-width: 100%;
        }

        @include sm-down {
          font-size: 20px;
        }

        @include xs-down {
          font-size: 18px;
        }

        span {
          font-weight: 500;
          font-style: italic;
          color: #ffffff;
        }
      }
    }
  }

  &__background {
    position: absolute;
    display: none;
    width: 100%;
    height: 610px;
    overflow: hidden;
    animation-duration: 1s;

    @media only screen and (min-width: 1921px) {
      max-width: 100%;
    }

    &.animated {

      @include md-up {
        display: block;
      }
    }

    @include md-down {
      display: none;
    }

    img {
      position: absolute;

      &:not(.rellax) {
        top: 50%;
        left: 50%;
        min-width: 1920px;
        height: 100%;
        transform: translate(-50%, -50%);
      }

      &.rellax {
        top: -150px;
        right: 0;
        left: auto;
        width: 1100px;
      }

      @media only screen and (min-width: 1921px) {
        width: 100%;
      }
    }

    &_left {
      top: 0;
      left: 0;
      z-index: 1;
    }

    &_right {
      top: -30px;
      right: 0;

      @include lg-down {
        width: 1054px;
      }
    }
  }

  &__slider {

    @include md-down {
      max-width: 100%;
    }

    @include xs-down {
      padding-left: 25px;
    }
  }

  &__slide {
    display: none;
    animation-duration: 0.5s;

    &.active {
      display: block;
    }
  }

  &__btn {
    position: absolute;
    bottom: 0;
    left: 50%;
    z-index: 3;
    transform: translateX(-50%);
    transition: 0.3s;
    opacity: 0;
    visibility: hidden;

    @include md-down {
      transform: translate(-50%, 50%);
    }

    &.visible {
      opacity: 1;
      visibility: visible;
    }
  }

  svg {
    position: absolute;
    bottom: 6px;
    right: 50%;
    font-size: 160px;
    color: rgba(255, 255, 255, 0.3);
    transition: opacity 0.3s, hidden 0.3s;
    opacity: 0;
    visibility: hidden;
    transform: translateX(52px);

    @include md-down {
      display: none;
    }

    &.invisible {
      opacity: 0;
      visibility: hidden;
    }

    &.active {
      opacity: 1;
      visibility: visible;
    }
  }
}