.chatbots {
  position: relative;
  text-align: center;
  padding-top: 90px;
  padding-bottom: 80px;
  overflow: hidden;

  @include md-down {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  @include xs-down {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  &__header {
    margin-bottom: 40px;

    @include md-down {
      margin-bottom: 30px;
    }

    p {
      font-size: 30px;
      font-weight: 300;
      margin-top: 0;
      margin-bottom: 40px;
      opacity: 0.5;

      @include md-down {
        font-size: 24px;
        margin-bottom: 30px;
      }

      @include sm-down {
        font-size: 20px;
      }

      @include xs-down {
        font-size: 18px;
      }
    }
  }

  &__tabs {
    margin-bottom: 165px;

    @include md-down {
      margin-bottom: 120px;
    }

    @include sm-down {
      margin-bottom: 60px;
    }

    &-header {
      margin-bottom: 140px;

      @include sm-down {
        margin-bottom: 30px;
      }

      ul {
        display: flex;
        list-style-type: none;
        padding-left: 0;
        margin-top: 0;
        margin-bottom: 0;

        @include xs-down {
          justify-content: space-between;
        }

        li {
          flex: 0 0 33.333%;
          padding-right: 5px;
          padding-left: 5px;

          a {
            display: inline-block;
            vertical-align: top;
            line-height: 1.4;
            letter-spacing: 0.5px;
            text-decoration: none;
            text-transform: uppercase;
            border-radius: 100px;
            padding-top: 10px;
            padding-right: 20px;
            padding-bottom: 10px;
            padding-left: 20px;
            margin-bottom: 10px;
            transition: 0.3s;

            @include sm-down {
              font-size: 14px;
              padding-top: 10px;
              padding-bottom: 10px;
            }

            @include xs-down {
              padding-right: 10px;
              padding-left: 10px;
            }

            &:hover {
              border-color: #0a9bdc;
            }
          }

          p {
            line-height: 1.4;
            font-style: italic;
            margin-top: 0;
            margin-bottom: 0;

            @include sm-down {
              font-size: 14px;
            }
          }

          &.mila {

            a {
              color: #0a9bdc;
              border: 2px solid rgba(#0a9bdc, 0.3);

              &:hover {
                border: 2px solid #0a9bdc;
              }
            }

            &.active {

              a {
                color: #ffffff;
                background-color: #0a9bdc;
                border-color: #0a9bdc;
              }
            }
          }

          &.max {

            a {
              color: #5fc8c3;
              border: 2px solid rgba(#5fc8c3, 0.3);
              
              &:hover {
                border: 2px solid #5fc8c3;
              }
            }

            &.active {

              a {
                color: #ffffff;
                background-color: #5fc8c3;
                border-color: #5fc8c3;
              }
            }
          }

          &.minnie {

            a {
              color: #6b80dc;
              border: 2px solid rgba(#6b80dc, 0.3);

              &:hover {
                border: 2px solid #6b80dc;
              }
            }

            &.active {

              a {
                color: #ffffff;
                background-color: #6b80dc;
                border-color: #6b80dc;
              }
            }
          }
        }
      }
    }

    &-content {
      position: relative;
      display: flex;
      justify-content: center;
      width: 630px;
      height: 630px;
      background-color: #ffffff;
      border: dashed 1px #a0c8d7;
      border-radius: 50%;
      padding-top: 100px;
      margin-right: auto;
      margin-left: auto;
      
      @include md-down {
        width: 550px;
        height: 550px;
        padding-top: 60px;
      }

      @include sm-down {
        width: auto;
        height: auto;
        background-color: transparent;
        border: none;
        border-radius: 0;
        padding-top: 0;
      }

      &:before {
        content: '';
        position: absolute;
        z-index: -1;
        top: 50%;
        left: 50%;
        width: 860px;
        height: 860px;
        background: url(../images/background.svg) no-repeat center / cover;
        opacity: 0.3;
        animation: spin 130s cubic-bezier(1, 1, 0, 0) infinite;

        @include md-down {
          width: 768px;
          height: 768px;
        }

        @include sm-down {
          display: none;
        }
      }
    }

    &-item {
      display: none;
      animation-duration: 0.5s;

      &_mila {

        .chatbots__bubble {

          &:nth-of-type(1) {
            top: 13px;
            right: 23px;

            @include md-down {
              top: 0;
              right: -27px;
            }
          }

          &:nth-of-type(2) {
            top: 175px;
            right: -104px;
          }

          &:nth-of-type(3) {
            right: -58px;
            bottom: 233px;

            @include md-down {
              bottom: 153px;
            }
          }

          &:nth-of-type(4) {
            top: 491px;
            right: -32px;
          }

          &:nth-of-type(5) {
            bottom: 16px;
            left: 15px;

            @include md-down {
              bottom: 29px;
              left: -45px;
            }
          }

          &:nth-of-type(6) {
            bottom: 207px;
            left: -58px;

            @include md-down {
              bottom: 165px;
            }
          }

          &:nth-of-type(7) {
            top: 210px;
            left: -76px;
          }

          &:nth-of-type(8) {
            top: 61px;
            left: -28px;

            @include md-down {
              left: -80px;
            }
          }
        }
      }

      &_max {

        .chatbots__tabs-item-content h3 {
          color: #5fc8c3;
        }

        .chatbots__robot span {
          color: #5fc8c3;
        }

        .chatbots__more {
          color: #5fc8c3;
          border-bottom: 1px solid rgba(#5fc8c3, 0.2);

          &:hover {
            border-bottom-color: #5fc8c3;
          }
        }

        .chatbots__bubble {

          &:nth-of-type(1) {
            top: 13px;
            right: 23px;

            @include md-down {
              top: 0;
              right: -27px;
            }
          }

          &:nth-of-type(2) {
            top: 62px;
            left: -60px;
          }

          &:nth-of-type(3) {
            right: -58px;
            bottom: 297px;

            @include md-down {
              bottom: 289px;
            }
          }

          &:nth-of-type(4) {
            top: 491px;
            right: -32px;

            @include md-down {
              top: 439px;
              right: -71px;
            }
          }

          &:nth-of-type(5) {
            bottom: 16px;
            left: 15px;

            @include md-down {
              bottom: 29px;
              left: -82px;
            }
          }

          &:nth-of-type(6) {
            bottom: 268px;
            left: -58px;

            @include md-down {
              bottom: 244px;
            }
          }
        }
      }

      &_minnie {

        .chatbots__tabs-item-content h3 {
          color: #6b80dc;
        }

        .chatbots__robot span {
          color: #6b80dc;
        }
        
        .chatbots__more {
          color: #6b80dc;
          border-bottom: 1px solid rgba(#6b80dc, 0.2);

          &:hover {
            border-bottom-color: #6b80dc;
          }
        }

        .chatbots__bubble {

          &:nth-of-type(1) {
            top: 67px;
            right: 11px;

            @include md-down {
              top: 9px;
              right: 58px;
            }
          }

          &:nth-of-type(2) {
            top: 172px;
            left: -114px;

            @include md-down {
              top: 132px;
              left: -88px;
            }
          }

          &:nth-of-type(3) {
            right: -84px;
            bottom: 322px;

            @include md-down {
              right: -76px;
              bottom: 276px;
            }
          }

          &:nth-of-type(4) {
            top: 554px;
            right: 122px;

            @include md-down {
              top: 492px;
            }
          }

          &:nth-of-type(5) {
            bottom: 70px;
            left: -40px;

            @include md-down {
              bottom: 69px;
              left: -86px;
            }
          }
        }
      }

      &-content {
        position: relative;
        z-index: 1;

        @include sm-down {
          margin-bottom: 30px;
        }

        h3 {
          font-size: 30px;
          line-height: 1;
          letter-spacing: 1px;
          font-weight: 300;
          text-transform: uppercase;
          color: #0a9bdc;
          margin-top: 0;
          margin-bottom: 30px;

          @include sm-down {
            font-size: 24px;
            margin-bottom: 20px;
          }
        }

        p {
          max-width: 330px;
          font-weight: 300;
          line-height: 1.5;
          margin-top: 0;
          margin-right: auto;
          margin-bottom: 20px;
          margin-left: auto;
          
          @include sm-down {
            max-width: 300px;
            font-size: 15px;
          }

          span {
            font-weight: 400;
            background-color: #e1f5ff;
          }
        }
      }

      &.active {
        display: block;
      }
    }
  }

  &__robot {
    position: relative;
    display: inline-block;
    vertical-align: top;
    margin-bottom: 40px;

    @include sm-down {
      margin-bottom: 30px;
    }

    img {
      vertical-align: top;
      width: 120px;
      height: 120px;
      
      @include sm-down {
        width: 100px;
        height: 100px;
      }
    }

    span {
      position: absolute;
      right: -10px;
      bottom: -10px;
      display: inline-block;
      vertical-align: top;
      color: #0a9bdc;
      background-color: #ffffff;
      border-radius: 50%;
      box-shadow: 0 0 10px rgba(45, 50, 55, 0.2);
      padding-top: 14px;
      padding-right: 14px;
      padding-bottom: 14px;
      padding-left: 14px;

      @include sm-down {
        right: -6px;
        bottom: -6px;
        padding-top: 10px;
        padding-right: 10px;
        padding-bottom: 10px;
        padding-left: 10px;
      }

      svg {

        @include sm-down {
          font-size: 18px;
        }
      }
    }
  }

  &__more {
    display: inline-block;
    vertical-align: top;
    font-weight: 400;
    line-height: 1.4;
    text-decoration: none;
    color: #0a9bdc;
    border-bottom: 1px solid rgba(#0a9bdc, 0.2);
    transition: 0.3s;

    &:hover {
      border-bottom-color: #0a9bdc;
    }
  }

  &__bubbles-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    @include sm-down {
      position: relative;
      top: auto;
      right: auto;
      bottom: auto;
      left: auto;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  &__bubble {
    position: absolute;
    display: inline-block;
    vertical-align: top;
    max-width: 265px;
    background-color: #e1f5ff;
    border-radius: 10px;
    padding-top: 10px;
    padding-right: 20px;
    padding-bottom: 10px;
    padding-left: 20px;

    @include sm-down {
      max-width: 211px;
      font-size: 14px;
      padding-right: 15px;
      padding-left: 15px;
    }

    @include sm-up {
      display: none;
    }

    @include sm-down {
      position: static;
      margin-bottom: 10px;
    }

    &.animated {

      @include sm-up {
        display: inline-block;
        vertical-align: top;
      }
    }

    &:last-child {
      margin-bottom: 0;
    }

    &:after {
      content: '';
      position: absolute;
      right: 20px;
      bottom: -10px;
      width: 0;
      height: 0;
      border-top: 10px solid transparent;
      border-bottom: 10px solid transparent;
      border-right: 10px solid #e1f5ff;

      @include sm-down {
        display: none;
      }
    }

    p {
      line-height: 1.4;
      margin-top: 0;
      margin-bottom: 0;
      opacity: 0.8;
    }
  }

  &__about {
    position: relative;
    margin-bottom: 80px;

    @include md-down {
      margin-bottom: 60px;
    }

    @include xs-down {
      margin-bottom: 40px;
    }

    &:before {
      content: '';
      position: absolute;
      bottom: -42px;
      left: 50%;
      width: 25px;
      height: 2px;
      background-color: #0a9bdc;
      transform: translateX(-50%);

      @include md-down {
        bottom: -32px;
      }

      @include xs-down {
        bottom: -22px;
      }
    }

    p {
      max-width: 890px;
      font-size: 24px;
      line-height: 1.4;
      font-weight: 300;
      margin-top: 0;
      margin-right: auto;
      margin-bottom: 0;
      margin-left: auto;

      @include md-down {
        font-size: 20px;
      }
      
      @include sm-down {
        font-size: 18px;
      }

      @include xs-down {
        font-size: 16px;
      }

      span {
        font-weight: 400;
        background-color: #e1f5ff;
      }
    }
  }

  &__partners {
    display: flex;
    flex-wrap: wrap;
    border-right: 1px solid #e6ebeb;
    border-bottom: 1px solid #e6ebeb;

    @include xs-down {
      display: block;
    }

    &-item {
      flex: 0 0 33.333%;
      min-height: 230px;
      border-top: 1px solid #e6ebeb;
      border-left: 1px solid #e6ebeb;
      padding-top: 20px;
      padding-right: 20px;
      padding-bottom: 20px;
      padding-left: 20px;

      @include sm-down {
        flex-basis: 50%;
        min-height: initial;
      }
      
      &:nth-of-type(6) {
        padding-right: 15px;
        padding-left: 15px;
        
        @include sm-down {
          padding-right: 20px;
          padding-left: 20px;
        }
      }

      &-image-wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 60px;
        margin-bottom: 20px;
      }

      p {
        font-weight: 300;
        line-height: 1.3;
        margin-top: 0;
        margin-bottom: 0;

        span {
          font-weight: 400;
          background-color: #e1f5ff;
        }
      }
    }
  }
}

@keyframes spin {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }

  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}