.footer {
  background-color: #0a9bdc;
  padding-top: 10px;
  padding-bottom: 10px;

  @include sm-down {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  &__inner {
    display: flex;
    align-items: center;

    @include sm-down {
      flex-direction: column;
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  &__side {

    @include sm-down {
      margin-bottom: 20px;
    }

    &_social {
      flex: 0 0 40%;

      @include sm-down {
        order: 1;
        margin-bottom: 0;
      }

      ul {
        display: flex;
        list-style-type: none;
        padding-left: 0;
        margin-top: 0;
        margin-bottom: 0;

        li {
          margin-right: 15px;

          &:last-child {
            margin-right: 0;
          }
        }

        a {
          display: inline-block;
          vertical-align: top;
          color: #ffffff;
        }
      }
    }

    &_logo {
      flex: 0 0 20%;
      text-align: center;

      svg {
        font-size: 40px;
        color: #ffffff;
      }
    }

    &_copyright {
      flex: 0 0 40%;
      text-align: right;

      p {
        font-weight: 300;
        color: #ffffff;
        opacity: 0.8;
        margin-top: 0;
        margin-bottom: 0;
      }
    }
  }
}