*,
*:before,
*:after {
  box-sizing: border-box;
}

.container {
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;

  @include lg-down {
    max-width: 960px;
  }

  @include md-down {
    max-width: 720px;
  }

  @include sm-down {
    max-width: 540px;
  }

  @include xs-down {
    max-width: 100%;
    padding-right: 15px;
    padding-left: 15px;
  }
}

html {
  -ms-overflow-style: scrollbar;
}

body {
  position: relative;
  min-width: 320px;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  color: #414b50;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  overflow-y: scroll;

  @include xs-down {
    font-size: 14px;
  }
}

.svg-sprite {
  display: none;
}

.title {
  font-size: 16px;
  letter-spacing: 0.5px;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
  color: #414b50;
  margin-top: 0;
  margin-bottom: 0;

  &:before {
    content: '/ ';
    color: #0a9bdc;
  }

  &:after {
    content: ' /';
    color: #0a9bdc;
  }

  &_white {
    color: #ffffff;

    &:before {
      color: #ffffff;
      opacity: 0.6;
    }

    &:after {
      color: #ffffff;
      opacity: 0.6;
    }
  }
}

.request-demo-btn {
  display: inline-block;
  vertical-align: top;
  font-size: 16px;
  text-decoration: none;
  font-weight: 500;
  line-height: 1;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #f42c6e;
  border: none;
  border-radius: 100px;
  box-shadow: 0 0 20px 0 rgba(65, 75, 80, 0.4);
  padding-top: 20px;
  padding-right: 30px;
  padding-bottom: 20px;
  padding-left: 30px;
  transition: background-color 0.3s;
  cursor: pointer;
  outline: none;

  @include xs-down {
    font-size: 13px;
    padding-top: 15px;
    padding-right: 25px;
    padding-bottom: 15px;
    padding-left: 25px;
  }

  &:hover {
    background-color: darken(#f42c6e, 10%);
  }

  &:focus, &:active {
    background-color: darken(#f42c6e, 20%);
  }

  &:disabled {
    opacity: 0.4;
    cursor: not-allowed;
  }

  &_floating {
    position: fixed;
    bottom: 30px;
    left: 50%;
    z-index: 9;
    transition: 0.3s;
    transform: translate(-50%, calc(100% + 200px));

    &.visible {
      transform: translateX(-50%);
    }
  }
}

.request-demo-btn-wrapper {
  position: absolute;
  top: 500px;
  left: 50%;
  z-index: 9;
  transform: translateX(-50%);
}

.mobile-nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  background-color: rgba(#0a9bdc, 0.97);
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;

  @include md-up {
    display: none;
  }

  ul {
    text-align: center;
    list-style-type: none;
    padding-left: 0;
    margin-top: 0;
    margin-bottom: 0;

    li {
      margin-bottom: 20px;

      &:nth-of-type(6) {
        position: relative;
        margin-bottom: 30px;

        &:before {
          content: '';
          position: absolute;
          bottom: -15px;
          left: 50%;
          width: 100%;
          height: 1px;
          background-color: #ffffff;
          opacity: 0.2;
          transform: translateX(-50%);
        }
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

    a {
      font-size: 24px;
      text-decoration: none;
      color: #ffffff;
      
      @include xs-down {
        font-size: 20px;
      }
    }
  }

  button {
    position: absolute;
    top: 25px;
    right: 0;
    z-index: 9;
    display: inline-block;
    vertical-align: top;
    width: 22px;
    height: 16px;
    background: none;
    border: none;
    padding-top: 0;
    padding-right: 0;
    padding-bottom: 0;
    padding-left: 0;
    transition: 0.3s;
    outline: none;
    cursor: pointer;
    
    @include xs-down {
      right: 15px;
    }

    &:before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      height: 2px;
      border-radius: 50px;
      background-color: #0a9bdc;
      box-shadow: 0 -8px 0 0 #0a9bdc;
      transform: translateY(-50%);
      transition: 0.3s;
    }

    &:after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      border-radius: 50px;
      background-color: #0a9bdc;
      transition: 0.3s;
    }

    &.toggled {
      z-index: 99;

      &:before {
        background-color: #ffffff;
        box-shadow: none;
        transform: translateY(-50%) rotate(45deg);
      }

      &:after {
        top: 50%;
        background-color: #ffffff;
        transform: translateY(-50%) rotate(-45deg);
      }
    }
  }

  &__inner {
    position: relative;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &.active {
    opacity: 1;
    visibility: visible;
  }
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 998;
  width: 100%;
  height: 100%;
  background-color: rgba(#00141e, 0.6);
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;

  &_visible {
    opacity: 1;
    visibility: visible;
  }
}