.icon {
	display: inline-block;
	vertical-align: top;
	height: 1em;
	width: 1em;
	fill: currentColor;
	&_sprite_book {
		font-size: (42/16)+rem;
		width: (48/42)+em; }
	&_sprite_bubble {
		font-size: (24/16)+rem;
		width: (24/24)+em; }
	&_sprite_car {
		font-size: (44/16)+rem;
		width: (71/44)+em; }
	&_sprite_cart {
		font-size: (24/16)+rem;
		width: (24/24)+em; }
	&_sprite_fb {
		font-size: (30/16)+rem;
		width: (30/30)+em; }
	&_sprite_graph {
		font-size: (52/16)+rem;
		width: (50/52)+em; }
	&_sprite_heart {
		font-size: (42/16)+rem;
		width: (52/42)+em; }
	&_sprite_in {
		font-size: (14/16)+rem;
		width: (14/14)+em; }
	&_sprite_lens {
		font-size: (20/16)+rem;
		width: (20/20)+em; }
	&_sprite_like {
		font-size: (18/16)+rem;
		width: (18/18)+em; }
	&_sprite_logo {
		font-size: (247/16)+rem;
		width: (370/247)+em; }
	&_sprite_robot {
		font-size: (20/16)+rem;
		width: (18/20)+em; }
	&_sprite_router {
		font-size: (42/16)+rem;
		width: (51/42)+em; }
	&_sprite_star {
		font-size: (22/16)+rem;
		width: (22/22)+em; }
	&_sprite_street {
		font-size: (57/16)+rem;
		width: (57/57)+em; }
	&_sprite_telegram {
		font-size: (30/16)+rem;
		width: (30/30)+em; }
	&_sprite_tw {
		font-size: (30/16)+rem;
		width: (30/30)+em; } }
