.modal {
  position: relative;
  display: none;
  background-color: #ffffff;
  margin-top: auto;
  margin-bottom: auto;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;

  &_visible {
    display: block;
    opacity: 1;
    visibility: visible;
  }

  &_learn-more {

    .modal {

      &__inner {
        width: 700px;
        padding-top: 30px;
        padding-right: 30px;
        padding-bottom: 30px;
        padding-left: 30px;

        @include md-down {
          width: 600px;
        }

        @include sm-down {
          width: 500px;
        }

        @include xs-down {
          width: 100%;
          padding-top: 15px;
          padding-right: 15px;
          padding-bottom: 15px;
          padding-left: 15px;
        }
      }

      &__header {
        text-align: center;
        margin-bottom: 30px;

        @include sm-down {
          margin-bottom: 20px;
        }

        h3 {
          font-size: 30px;
          font-weight: 300;
          line-height: 1;
          letter-spacing: 1px;
          text-transform: uppercase;
          color: #0a9bdc;
          margin-top: 0;
          margin-bottom: 30px;

          @include sm-down {
            font-size: 24px;
            margin-bottom: 20px;
          }
        }
      }

      &__robot {
        position: relative;
        display: inline-block;
        vertical-align: top;

        img {
          vertical-align: top;
          width: 120px;
          height: 120px;
        }

        span {
          position: absolute;
          right: -10px;
          bottom: -10px;
          display: inline-block;
          vertical-align: top;
          color: #0a9bdc;
          background-color: #ffffff;
          border-radius: 50%;
          box-shadow: 0 0 10px rgba(45, 50, 55, 0.2);
          padding-top: 14px;
          padding-right: 14px;
          padding-bottom: 14px;
          padding-left: 14px;

          @include sm-down {
            right: -6px;
            bottom: -6px;
            padding-top: 10px;
            padding-right: 10px;
            padding-bottom: 10px;
            padding-left: 10px;
          }

          svg {

            @include sm-down {
              font-size: 18px;
            }
          }
        }
      }

      &__content {

        p {
          font-weight: 300;
          margin-top: 0;
          margin-bottom: 20px;

          span {
            font-weight: 400;
            background-color: #e1f5ff;
          }

          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }

    &_max {

      .modal__header h3 {
        color: #5fc8c3;
      }

      .modal__robot span {
        color: #5fc8c3;
      }
    }

    &_minnie {

      .modal__header h3 {
        color: #6b80dc;
      }

      .modal__robot span {
        color: #6b80dc;
      }
    }
  }

  &_request {
    padding-top: 80px;
    padding-right: 30px;
    padding-bottom: 60px;
    padding-left: 30px;

    @include sm-down {
      padding-top: 40px;
      padding-right: 15px;
      padding-bottom: 40px;
      padding-left: 15px;
    }

    @include xs-down {
      width: 100%;
    }

    .modal {

      &__inner {
        width: 500px;

        @include xs-down {
          width: 100%;
        }
      }

      &__logo {
        position: absolute;
        top: 0;
        left: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 90px;
        height: 90px;
        color: #ffffff;
        background-color: #0a9bdc;
        border-radius: 50%;
        transform: translate(-50%, -50%);

        @include sm-down {
          width: 70px;
          height: 70px;
        }

        svg {
          font-size: 40px;

          @include sm-down {
            font-size: 30px;
          }
        }
      }
    }
  }

  &__close {
    position: absolute;
    top: 0;
    right: -40px;
    width: 30px;
    height: 30px;
    font-size: 0;
    background: none;
    border: none;
    padding: 0;
    outline: none;
    cursor: pointer;

    @include sm-down {
      right: -30px;
      width: 24px;
      height: 24px;
    }

    @include xs-down {
      top: -30px;
      right: 0;
    }

    &:hover {

      &:before,
      &:after {
        opacity: 1;
      }
    }

    &:before,
    &:after {
      content: '';
      position: absolute;
      display: inline-block;
      vertical-align: top;
      width: 40px;
      height: 2px;
      background-color: #ffffff;
      opacity: 0.6;

      @include sm-down {
        width: 34px;
        height: 1px;
      }
    }

    &:before {
      transform: translate(-50%, -50%) rotate(45deg);
    }

    &:after {
      transform: translate(-50%, -50%) rotate(-45deg);
    }
  }
}

.modals-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100vh;
  padding-top: 30px;
  padding-bottom: 30px;
  overflow-x: hidden;
  overflow-y: auto;
  opacity: 0;
  visibility: hidden;

  @include xs-down {
    padding-top: 54px;
  }

  &_visible {
    opacity: 1;
    visibility: visible;
  }
}