.contact {
  position: relative;
  text-align: center;
  overflow: hidden;

  &__inner {
    padding-top: 120px;
    padding-bottom: 160px;

    @include xs-down {
      padding-top: 50px;
      padding-bottom: 50px;
    }

    p {
      max-width: 860px;
      font-size: 30px;
      font-weight: 300;
      line-height: 1.7;
      margin-top: 0;
      margin-right: auto;
      margin-bottom: 40px;
      margin-left: auto;
      opacity: 0.5;

      @include md-down {
        max-width: 660px;
        font-size: 24px;
      }

      @include sm-down {
        font-size: 20px;
        margin-bottom: 20px;
      }

      @include xs-down {
        font-size: 16px;
      }
    }
  }

  &__title {
    margin-bottom: 70px;

    @include sm-down {
      margin-bottom: 40px;
    }
  }

  &__btn {
    margin-bottom: 50px;
    
    @include xs-down {
      margin-bottom: 30px;
    }
  }

  &__whitepaper {
    line-height: 1.4;
    color: #0a9bdc;

    a {
      display: inline-block;
      vertical-align: top;
      text-decoration: none;
      color: #0a9bdc;
      border-bottom: 1px solid rgba(#0a9bdc, 0.2);
      transition: 0.3s;

      &:hover {
        border-bottom-color: #0a9bdc;
      }
    }
  }
}