.form {

  &__fields-wrapper {
    margin-bottom: 30px;

    @include xs-down {
      margin-bottom: 20px;
    }
  }

  &__field {
    margin-bottom: 40px;

    @include xs-down {
      margin-bottom: 20px;
    }

    &:last-child {
      margin-bottom: 0;
    }

    input {
      width: 100%;
      font-size: 20px;
      font-weight: 100;
      line-height: 1.5;
      color: #32373c;
      border: none;
      border-bottom: 1px solid rgba(#0a9bdc, 0.2);
      border-radius: 0;
      -webkit-appearance: none;
      transition: 0.3s;
      outline: none;

      @include xs-down {
        font-size: 16px;
      }

      &:focus {
        border-bottom: 1px solid #0a9bdc;
      }

      &::placeholder {
        color: #879196;
      }

      &.mce_inline_error {
        border-bottom-color: #f5143c !important;

        &:focus {
          border-bottom-color: #f5143c !important;
        }
      }
    }
  }

  div.mce_inline_error {
    font-size: 12px;
    line-height: 1.5;
    font-weight: 400 !important;
    font-style: italic;
    color: #f5143c !important;
    background-color: transparent !important;
    padding: 0 !important;
    margin: 6px 0 0 !important;
  }

  &__actions {
    text-align: center;
  }

  .response {
    position: absolute;
    left: 0;
    bottom: 30px;
    width: 100%;
    text-align: center;
    transition: 0.3s;
    transform: translateY(50%);

    @include xs-down {
      font-size: 14px;
      bottom: 20px;
    }

    a {
      display: inline-block;
      vertical-align: top;
      text-decoration: none;
      color: #0a9bdc;
      border-bottom: 1px solid rgba(#0a9bdc, 0.2);
      transition: 0.3s;

      &:hover {
        border-bottom-color: #0a9bdc;
      }
    }
  }
}