.ai-rd {
  position: relative;
  min-height: 830px;
  padding-top: 90px;

  @include lg-down {
    padding-top: 110px;
  }

  @include md-down {
    min-height: initial;
    background-color: rgba(16, 151, 212, 0.97);
    padding-top: 40px;
    padding-bottom: 40px;
  }

  @include xs-down {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  &__inner {
    position: relative;
    z-index: 9;
  }

  &__title {
    margin-bottom: 40px;

    @include xs-down {
      margin-bottom: 30px;
    }
  }

  &__content {

    h3 {
      font-size: 36px;
      font-weight: 300;
      line-height: 1.4;
      color: #ffffff;
      margin-top: 0;
      margin-bottom: 40px;

      @include md-down {
        font-size: 32px;
        margin-bottom: 30px;
      }

      @include sm-down {
        font-size: 28px;
      }

      @include xs-down {
        font-size: 22px;
        margin-bottom: 30px;
      }
    }
  }

  &__item {
    display: flex;
    max-width: 760px;
    background-image: linear-gradient(to right, rgba(#fff, 0.4) 60%, rgba(#fff, 0) 0%);
    background-position: bottom;
    background-size: 8px 1px;
    background-repeat: repeat-x;
    padding-bottom: 20px;
    margin-bottom: 20px;

    @include lg-down {
      max-width: 700px;
    }

    &:last-child {
      background: none;
      padding-bottom: 0;
      margin-bottom: 0;
    }

    &-number {
      flex: 0 0 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 40px;
      font-size: 18px;
      font-weight: 500;
      color: #ffffff;
      border: 1px solid rgba(#ffffff, 0.6);
      border-radius: 50%;
      margin-right: 20px;

      @include xs-down {
        flex: 0 0 30px;
        width: 30px;
        height: 30px;
        font-size: 16px;
        margin-right: 10px;
      }
    }

    p {
      font-size: 20px;
      font-weight: 300;
      line-height: 1.5;
      color: rgba(#ffffff, 0.8);
      margin-top: 0;
      margin-bottom: 0;

      @include sm-down {
        font-size: 18px;
      }

      @include xs-down {
        font-size: 16px;
      }

      span {
        font-weight: 500;
        font-style: italic;
        color: #ffffff;
      }
    }
  }

  &__background {
    position: absolute;
    height: 830px;

    @media only screen and (min-width: 1921px) {
      max-width: 100%;
    }

    @include md-down {
      display: none;
    }

    img {
      position: absolute;

      @media only screen and (min-width: 1921px) {
        width: 100%;
      }

      &.centered-rellax {
        top: -150px;
        right: 0;
        left: auto;
        width: 1100px;
      }
    }

    &_left {
      top: 60px;
      left: 0;
      z-index: 1;
      width: 100%;
      padding-bottom: 30px;
      overflow: hidden;

      img {
        top: 50%;
        left: 50%;
        min-height: 830px;
        min-width: 1920px;
        transform: translate(-50%, -50%);
      }

      svg {
        position: absolute;
        right: 50%;
        bottom: 6px;
        font-size: 160px;
        color: rgba(255, 255, 255, 0.3);
        transform: translateX(469px);
      }
    }

    &_right {
      top: 30px;
      right: 0;
      width: 100%;
      text-align: right;
      overflow: hidden;

      img {
        left: 50%;
        bottom: 0;
        transform: translateX(-50%);
      }
    }
  }

  &__background-image-wrapper {
    position: relative;
    width: 100%;
    height: 830px;
    overflow: hidden;
  }
}