.future-technologies {
  padding-top: 90px;
  padding-bottom: 20px;

  @include md-down {
    padding-top: 40px;
    padding-bottom: 0;
  }

  @include xs-down {
    padding-top: 30px;
  }

  &__title {
    margin-bottom: 60px;

    @include md-down {
      margin-bottom: 40px;
    }

    @include xs-down {
      margin-bottom: 30px;
    }
  }

  &__content {
    margin-bottom: 90px;

    @include md-down {
      margin-bottom: 60px;
    }

    p {
      position: relative;
      font-size: 24px;
      font-weight: 300;
      line-height: 1.4;
      padding-left: 55px;
      margin-top: 0;
      margin-bottom: 40px;

      @include md-down {
        font-size: 20px;
        margin-bottom: 30px;
      }

      @include sm-down {
        font-size: 18px;
      }

      @include xs-down {
        font-size: 16px;
        padding-left: 25px;
      }

      &:last-child {
        margin-bottom: 0;

        @include md-down {
          margin-bottom: 0;
        }
      }

      &:before {
        content: '';
        position: absolute;
        top: 13px;
        left: 0;
        width: 25px;
        height: 2px;
        background-color: #0a9bdc;

        @include xs-down {
          width: 15px;
        }
      }

      span {
        font-weight: 400;
        background-color: #e1f5ff;
      }
    }
  }

  &__icons {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-left: 45px;

    @include xs-down {
      padding-left: 0;
    }

    &-item {
      flex: 0 0 33.333%;
      position: relative;
      text-align: center;
      line-height: 1.5;
      margin-bottom: 60px;

      @include sm-down {
        flex: 0 0 50%;
      }

      .wrapper {
        position: relative;
        width: max-content;

        @include sm-down {
          width: 100%;
        }
      }

      .icon-wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 60px;

        svg {
          fill: none
        }
      }

      p {
        margin-top: 0;
        margin-bottom: 0;
      }

      img {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
      }

      &_router {

        .wrapper {
          width: 181px;

          @include sm-down {
            width: 100%;
          }
        }

        svg {
          margin-bottom: 35px;
        }

        img {
          top: -20px;
          width: 118px;
          height: 96px;
        }
      }

      &_street {

        .wrapper {
          margin-right: auto;
          margin-left: auto;
        }

        svg {
          margin-bottom: 15px;
        }

        img {
          top: -20px;
          width: 122px;
          height: 101px;
        }
      }

      &_heart {

        .wrapper {
          width: 212px;
          margin-left: auto;

          @include sm-down {
            width: 100%;
          }
        }

        svg {
          margin-bottom: 15px;
        }

        img {
          top: -36px;
          width: 89px;
          height: 140px;
        }
      }

      &_book {

        .wrapper {
          width: 181px;

          @include sm-down {
            width: 100%;
          }
        }

        svg {
          margin-bottom: 20px;
        }

        img {
          top: -25px;
          width: 97px;
          height: 117px;
        }
      }

      &_graph {

        .wrapper {
          margin-right: auto;
          margin-left: auto;
        }

        svg {
          margin-bottom: 20px;
        }

        img {
          top: -20px;
          width: 136px;
          height: 96px;
        }
      }

      &_car {

        .wrapper {
          margin-left: auto;
        }

        svg {
          margin-bottom: 20px;
        }

        img {
          top: -20px;
          width: 108px;
          height: 111px;
        }
      }
    }
  }
}