.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  background-color: rgba(#ffffff, 0.98);
  padding-bottom: 25px;

  @include md-down {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  &_hidden {
    transform: translateY(-100%);
  }

  &_sticky {
    position: fixed;
    z-index: 99;
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
    padding-top: 15px;
    padding-bottom: 15px;
    transition: transform 0.3s;

    .header__inner {
      align-items: center;
    }

    .header__logo {
      padding-top: 0;

      img {
        height: 30px;
      }
    }

    .header__main-nav {
      padding-top: 0;

      a {

        &.active {
          border-bottom-color: #f42c6e;
        }
      }
    }
  }

  &__inner {
    display: flex;
    justify-content: space-between;

    @include md-down {
      align-items: center;
    }
  }

  &__logo {
    padding-top: 24px;

    @include md-down {
      padding-top: 0;
    }

    img {
      height: 40px;
      vertical-align: top;
    }
  }

  &__main-nav {
    padding-top: 20px;

    @include md-down {
      padding-top: 0;
    }

    ul {
      display: flex;
      list-style-type: none;
      padding-left: 0;
      margin-top: 0;
      margin-bottom: 0;

      @include md-down {
        display: none;
      }
    }

    li {
      margin-right: 25px;

      &:nth-of-type(6) {
        position: relative;
        margin-right: 50px;

        &:before {
          content: '';
          position: absolute;
          top: 50%;
          right: -25px;
          width: 1px;
          height: 30px;
          background-color: #879196;
          opacity: 0.2;
          transform: translateY(-50%);
        }
      }

      &:last-child {
        margin-right: 0;
        opacity: 0.5;
      }
    }

    a {
      font-size: 15px;
      color: #2d3237;
      text-decoration: none;
      border-bottom: 1px solid transparent;
      padding-bottom: 3px;
      transition: 0.3s;

      &:hover {
        border-bottom-color: #f42c6e;
      }
    }

    button {
      position: relative;
      z-index: 9;
      display: none;
      width: 22px;
      height: 16px;
      background: none;
      border: none;
      padding-top: 0;
      padding-right: 0;
      padding-bottom: 0;
      padding-left: 0;
      transition: 0.3s;
      outline: none;
      cursor: pointer;

      @include md-down {
        display: block;
      }

      &:before {
        content: "";
        position: absolute;
        top: 50%;
        left: 0;
        width: 100%;
        height: 2px;
        border-radius: 50px;
        background-color: #0a9bdc;
        box-shadow: 0 -8px 0 0 #0a9bdc;
        transform: translateY(-50%);
        transition: 0.3s;
      }

      &:after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2px;
        border-radius: 50px;
        background-color: #0a9bdc;
        transition: 0.3s;
      }

      &.toggled {
        z-index: 99;

        &:before {
          background-color: #ffffff;
          box-shadow: none;
          transform: translateY(-50%) rotate(45deg);
        }

        &:after {
          top: 50%;
          background-color: #ffffff;
          transform: translateY(-50%) rotate(-45deg);
        }
      }
    }
  }
}